import React from 'react';

const Home = () => {
  return (
    <div className="screen-container">
      <p>
        Na CesteSNP.sk pracujeme dňom a nocou, teda pokiaľ nie sme akurát na horách, v práci, v
        knižnici, v divadle, u frajerky, či sa hráme s našimi deťmi...
      </p>
      <p>
        Sekcia <a href="/pred/articles/1">Pred Cestou</a> je prvá kde môžeš vidieť výsledky našej
        práce! Táto sekcia má hlavný cieľ zjednodušiť plánovanie tvojej Cesty hrdinov SNP. Pozri sa
        na to a daj nám vedieť ako ti to pomohlo s plánovaním. Ak chceš sledovať náš pokrok môžeš
        sem samozrejme pravidelne chodiť alebo sledovať naše novinky na{' '}
        <a target="_blank" href="https://www.facebook.com/CestaSNPsk-185536644838/">
          Facebooku
        </a>.
      </p>
      <p>
        Taktiež sa k nám môžeš pridať a pomocť s vývojom. Používame moderné technológie ako React,
        JavaScript ES6, Node.js a NoSQL databázu. To všetko beží na Heroku cloude a tak máš veľkú
        šancu sa naučiť čosi nové. Ak to ale už všetko poznáš, vôbec neváhaj sa ozvať a naopak
        naučiť nás ako postaviť CestaSNP.sk na nohy rýchlo a efektívne. Kontakt na nás:{' '}
        <a target="_blank" href="mailto:info@cestasnp.sk">
          info@cestasnp.sk
        </a>.
      </p>
      <p>
        CestuSNP.sk nájdeš na{' '}
        <a target="_blank" href="https://github.com/hasty001/cestasnp-web">
          Githube
        </a>.
      </p>
    </div>
  );
};

export default Home;



